import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import CardActionArea from "@mui/material/CardActionArea";

const dispatchedEvents = [];

const SrsMuiCardActionArea = function (props, ref) {
  return (
    <CardActionArea {...props} theme={props.muiTheme}>
      <slot />
    </CardActionArea>
  );
};

const styles = `
  /* Custom styles for the MuiCardActionArea component */
`;

const props = [
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableRipple",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to CardActionArea if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCardActionArea),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
